@import url(https://fonts.googleapis.com/css?family=Raleway:300,400,700,900);
* {
    box-sizing: border-box;
}

/*####################### general #####################*/
html {
    scroll-behavior: smooth;
    font-family: 'Raleway', sans-serif;
}

body {
    background-color: rgb(60, 66, 75);
    color: white;
    text-align: center;
    margin: 0;
    //display: flex;
    //min-height: 100vh;
    //flex-direction: column;
}
main {
    width: 100%;
    max-width: 960px;
    margin: auto;
    padding-bottom: 7rem;
    display: block;
    flex: 1 0 auto;
}

p {
    font-size: 2rem;
}

header {
    text-align: left;
}

h1 {
    max-width: 100%;
    margin: auto;
    padding-top: 15rem;
    margin-bottom: 3rem;    
    font-size: 9rem;
    font-weight: 400;  
}

li {
    text-decoration: none;
}


ul {
    margin: auto;
    padding: em;
}
li {
    list-style: none;
    display: inline-block;
    padding: 0em 1rem;
}
a {
    text-decoration: none; 
    color: white;
    font-size: 2rem;
}
a:hover {
    color: gray;
}
.Home_aboutMe__1DsVj {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: auto;
    margin-top: 10rem;
    padding: 1.5rem;
    font-size: 2rem; 
    color: rgb(158, 166, 173);
    border-top: dotted;
    border-color: rgba(240, 248, 255, 0.144);
}
.PortfolioItem_portItem__yloxh {
    position: relative;
    margin-bottom: 10%;
}
.PortfolioItem_portItem__yloxh img {
    width: 90%;
    border-bottom-right-radius: 80%;
    border-top-left-radius: 15%;
    border-top-right-radius: 15%;
    border-bottom-left-radius: 15%;
    -webkit-filter: blur(0  px);
            filter: blur(0  px);
    transition: border-radius .5s, -webkit-filter .5s;
    transition: border-radius .5s, filter .5s;
    transition: border-radius .5s, filter .5s, -webkit-filter .5s;
}
.PortfolioItem_portItem__yloxh:hover img{
    border-radius: 5%;
    -webkit-filter: blur(0px);
            filter: blur(0px);
}
.PortfolioItem_portDesc__2T6UI {
    position: absolute;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    z-index: 1;
    bottom: 1.5em;
    color: rgba(255, 255, 255, 0);
    -webkit-backdrop-filter: blur(0px);
    backdrop-filter: blur(0px);
    padding-bottom: .5em;
    padding-top: .5em;
    margin: 0;
    transition: color .2s, background-color .2s, -webkit-backdrop-filter .1s ease .1s;
    transition: color .2s, backdrop-filter .1s ease .1s, background-color .2s;
    transition: color .2s, backdrop-filter .1s ease .1s, background-color .2s, -webkit-backdrop-filter .1s ease .1s;
}
.PortfolioItem_portItem__yloxh:hover .PortfolioItem_portDesc__2T6UI {
    color: white;
    background-color: rgba(255, 255, 255, 0.027);
    -webkit-backdrop-filter: blur(12px);
    backdrop-filter: blur(12px) ;
}
.PortfolioItem_portDesc__2T6UI p {
    font-size: 200%;
    margin: .1em 0.5em;
}
.PortfolioList_category__31QOT {
    width: -webkit-fit-content;
