.TimelineItemLeft_item__3ieUT {
    left: 0;
    max-width: 100%;
    padding: 10px 40px;
    position: relative;
    background-color: inherit;
    width: 50%;   
    
  
}
.TimelineItemLeft_item__3ieUT::before { /* arrows  */
    content: '';
    
    height: 0;
    position: absolute;
    top: 13px;
    width: 0;
    z-index: 1;
    border: medium solid white;
    right: 50px;    
    border-width: 10px 0 10px 17px;
    border-color: transparent transparent transparent white;
}
.TimelineItemLeft_item__3ieUT::after { /* circles */
    content: '';
    position: absolute;
    width: 25px;
    height: 25px;
    right: -17px;
    background-color: rgb(60, 66, 75);
    border: 4px solid white;
    top: 5px;
    border-radius: 50%;
    z-index: 1;
}

.TimelineItemLeft_content__1bZv7 {
    position: relative; 
    right: 35px;
    padding: 25px 30px; 
    border: 3px solid white;
    border-radius: 25px;
    border-top-right-radius: 0;
    
}
.TimelineItemLeft_content__1bZv7 h2 {
    margin: 0;
    font-size: 2rem;
}
.TimelineItemLeft_content__1bZv7 p {
    margin: 0;
    margin-top: 1rem;
    font-size: 1.5rem;
}
